.big-zindex{ 
  z-index: 4000 !important;
} 
.ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item {
  padding-right: 40px;
}

.flex-row-container {
  display: flex;
  overflow: hidden;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}

.flex-col-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
}

.flex-row-item+.flex-row-item {
  margin: 0px;
  margin-left: 5px;
}

.flex-col-item+.flex-col-item {
  margin: 0px;
  margin-top: 5px;
}

.flex-fixed-item {
  flex-grow: 0;
  flex-shrink: 0;
}

.flex-grow-item {
  flex-grow: 2;
  overflow: auto;
}

.flex-y-grow-item {
  height: 0;
  flex-grow: 2;
  flex-shrink: 0;
}

.flex-x-grow-item {
  width: 0;
  flex-grow: 2;
  flex-shrink: 0;
}




