
.bpmn-dialog * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.bpmn-dialog .el-dialog__header {
  border-bottom: 1px solid #e5e5e5 !important;
  padding: 10px !important;
}
.bpmn-dialog .el-dialog__headerbtn {
  top: 15px !important;
  right: 15px !important;
}
.bpmn-dialog .content {
  position: relative;
}
.bpmn-dialog .el-dialog__body {
  padding: 0px !important;
}
.bpmn-dialog .headers {
  margin: 5px;
}
.bpmn-dialog .title-label {
  text-align: center;
  font-family: Microsoft YaHei, SimSun !important;
  font-weight: 300 !important;
}
.bpmn-dialog .title-number {
  font-size: 14px;
  color: #666666;
  font-family: Microsoft YaHei, SimSun;
}
.bpmn-dialog .el-dialog__footer {
  padding: 15px 15px !important;
  border-top: 1px solid #e5e5e5 !important;
}



